@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif; }

html,
body,
#root {
  width: 100%;
  height: 100%;
  padding: 40px;
  background-image: linear-gradient(to bottom right, #333, #666); }

.app-container {
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .app-container .main-container {
    min-width: 300px;
    background-image: linear-gradient(#4b637d, #25313d);
    height: 100%;
    display: flex;
    flex-direction: column;
    border-radius: 16px;
    box-shadow: 0px 20px 0px 7px #131313, 0px 0px 0px 7px #131313, 0px 20px 0px 10px #000, 0px 0px 0px 10px #000; }
  .app-container .top-section {
    flex: 0 0 50%; }
  .app-container .bottom-section {
    flex: 1 1; }

.btn {
  border: 1px solid #ddd;
  cursor: pointer; }
  .btn:focus {
    outline: none; }

.popup-container {
  position: absolute;
  will-change: transform;
  top: -52px;
  left: -29px;
  -webkit-transform: translate3d(487px, 177px, 8px);
          transform: translate3d(487px, 177px, 8px);
  width: 12em;
  height: 5.5em;
  display: flex;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 5px 2px rgba(15, 15, 15, 0.2);
  font-size: 16px; }

.form-container {
  padding: 6px;
  width: 100%; }
  .form-container label {
    font-size: 11px; }
  .form-container input {
    border-radius: 2px;
    border: 1px solid rgba(15, 15, 15, 0.2); }

h2 {
  text-align: center;
  color: #fff;
  border-top: 1px solid #70737d;
  border-bottom: 1px solid #70737d;
  margin: 0;
  padding: 6px; }

.top-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 300px; }
  .top-container .title {
    color: #fff;
    font-size: 40px;
    margin-top: 6px; }

.weather-container {
  color: #fff; }
  .weather-container .header {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: 25px;
    margin: 8px; }
  .weather-container .inner-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: #fff;
    align-items: center; }
    .weather-container .inner-container .current-weather {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      font-size: 60px;
      margin: 8px; }
    .weather-container .inner-container .image {
      width: 9em;
      height: 9em;
      margin: 8px; }
      .weather-container .inner-container .image img {
        width: 100%;
        height: 100%; }
  .weather-container .footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: 25px;
    margin: 8px; }
  .weather-container .today-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 25px;
    margin: 8px; }
    .weather-container .today-info .title {
      float: left; }
    .weather-container .today-info .info {
      width: 15vw; }
      .weather-container .today-info .info .title2 {
        float: left;
        font-weight: 700; }
      .weather-container .today-info .info .value {
        float: right; }

.btn-select-location {
  font-size: .825em;
  text-decoration: none;
  font-weight: 700;
  padding: .35em 1em;
  background-color: #40DCC2;
  border-radius: 1.25em;
  color: #222;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.7);
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 2px rgba(0, 0, 0, 0.2), inset 0 1px 2px rgba(255, 255, 255, 0.7); }

.btn-select-location:hover {
  background-color: #49FCDF; }

.btn-select-location:active {
  background-color: #247D6E;
  background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.2), inset 0 2px 5px rgba(0, 0, 0, 0.2), 0 1px rgba(255, 255, 255, 0.2); }

